body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.image img {
  height: 50vh;
  width: 20vw;
}

.client-area {
  display: flex;
  justify-content: space-between;
}

.services-log {
  border: 1px solid beige;
  width:50vw;
  padding: 3vh;
  background-color: beige;
  border-radius: 10px;
  margin-right: 2vw;
}

.logs {
  border: 1px solid beige;
  background-color: white;
  border-radius: 0 50px;
  margin-top: 2vh;
  margin-bottom: 3vh;
  padding-top: 2vh;
  padding-bottom: 1vh;
}

.logs ul li {
  list-style: none;
  font-size: 15px;
}

.logs span {
  color: orangered;
}

.logs button {
  color:  white;
  background-color: orangered;
  border-radius: 0 10px;
  border: none;
  padding-left: 2vw;
  padding-right: 2vw;
  padding-bottom: 5px;
  padding-top: 5px;
  margin-right: 2vw;
  margin-top: 1vh;
  font-size: small;
}

.card-2 img {
  height: 100px;
  width: 100px;
  margin-top: 20px;
  margin-left: 5px;
  float: left;
  padding: 0 20px 20px 0;
}

.card-2 {
  display: flex;
  flex-direction: row;
}

.card-2 span {
  color: orangered;
  list-style: none;
}

.overflow {
  height: 167vh;
  overflow-y: scroll;
}

.loginpage {
  border: 1px solid grey;
  width: 50vw;
  margin: auto;
  margin-top: 20vh;
  margin-bottom: 20vh;
  padding-top: 3vh;
  padding-bottom: 3vh;
  border-radius: 50px;
  background-color: #552607ab;
}

.bg-img{

  background-image:url('https://images.unsplash.com/photo-1505691723518-36a5ac3be353?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8Nnx8aG9tZXxlbnwwfHwwfHw%3D&auto=format&fit=crop&w=700&q=60');
  min-height: 90vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

}

.container{
    position: absolute;
    margin:0;
    width: 100%;
    
} 

.txt{
  text-align: left;
  font-size: 40;
  color:white;

}

.txt-2{
  text-align: left;
  color:#ff4500

}

.sub{
  text-align: left;
  margin-top: 5vh;
  color:white;
  margin-bottom: 2px;

}

.home-tab {
  padding: 5vh;
  padding-right: 7vw;
  border-radius: 60px 0;
  width: 60vw;
  margin: auto;
  display: flex;
  flex-direction: column;
  margin-top: 18vh;
  background-color: #4b2004a1;
}

.home-tab button, .nav-icon button{
  background-color: orangered;
}

.nav-color {
  background-color: #4b2004a1;
  height: 10vh;
  color: white;

}

.nav-icon .icon {
  margin-right: px;
  margin-right: 4vw;
}

.nav-icon {
  display: flex;
  
}

.nav-icons {
  display: flex;
