  color:white;
  /* background-color:burlywood; */
}
.navbar-nav ul li{
  display: flex;
  float:left;
}


.show{
    text-align: center;
    color: #000;
    margin: 3vh;
}

.color {
    background-color: beige;
    padding-top: 1vh;
