}

.service-cards {
    border: 1px solid rgb(223, 219, 219);
    background-color: white;
    min-width: 18vw;
    max-height: 18rem;
    min-height: 14rem;
    padding-top: 20px;
    padding-right: 20px;
    padding-left: 10px;
    padding-bottom: 8px;
    text-align: left;
    margin-bottom: 10px;
    border-radius: 0 0 50px;
    margin-right: 3vw;
}

.flex {
    display: flex;
    justify-content: center;
    width: 65vw;
}

.service-cards-two {
